import { useEffect,useState } from 'react';
import classes from './AvailableMeals.module.css';
import Card from '../UI/Card';
import useHttpRequest from '../../hooks/use-http';
import MealItem from './MealItem/MealItem';


const AvailabelMeals = () => {

const {isLoading, error, sendRequest:fetchMeals} = useHttpRequest();
const [meals,setMeals] = useState([]);

useEffect(()=>{
  const mealListsDisplay = (meals) =>{
    const loadedMeals = [];
    for (const key in meals){
      loadedMeals.push({
        id:key,
        name:meals[key].name,
        description:meals[key].description,
        price:meals[key].price
      });
    }
     setMeals(loadedMeals);   
  }

  fetchMeals({
    method : 'GET',
    url: 'https://cart-react-a55c2-default-rtdb.firebaseio.com/meals.json '
  },mealListsDisplay); 
  
},[fetchMeals]);

  const mealLists = meals.map((meal) => <MealItem key={meal.id} id= {meal.id} name= {meal.name}
     description= {meal.description} price = {meal.price} />) ; 


    return (

    <section className= {classes.meals}>
         <Card>
           {isLoading && <section><p>Loading</p></section>}
           {error && <section><p>{error}</p> </section>}
            {!error && !isLoading &&
            <ul>
                 {mealLists}

                 </ul> }
       
        </Card>
    </section>
  
    )
}
export default AvailabelMeals;