import React , {useContext,useState} from 'react';
import classes from './Cart.module.css';
import Modal from '../UI/Modal';
import CartContext from '../../store/cart-context';
import CartItem from './CartItem';
import Checkout from './Checkout';
import useHttpRequest from '../../hooks/use-http';


const Cart = props =>{

    const [isCheckout,setIsCheckout] = useState(false);
    const [isUserDataAdded,setIsUserDataAdded] = useState(false);
   const cartCtx =  useContext(CartContext);

   const totalAmount = `$${cartCtx.totalAmount.toFixed()}`;
   const hasItems =cartCtx.items.length >0 ;

   const cartItemAddHandler = item =>{

    cartCtx.addItem({...item, amount :1});

   }

   const cartItemRemoveHandler = id => {   
       
    cartCtx.removeItem(id);

   }

   const {isLoading,error,sendRequest:postUserData} = useHttpRequest();

   const submitOrderHandler = (userData)=>{

    const showData =(data)=>{
        console.log(data);
        setIsUserDataAdded(true);
    };
       postUserData({
        method : 'POST',
        url: 'https://cart-react-a55c2-default-rtdb.firebaseio.com/orders.json',
        body : {user:userData,orderedItems:cartCtx.items}
      },showData); 

      cartCtx.clearItem();
    
    }

    const orderHandler = () =>{
        setIsCheckout(true);
   }

    const cartItems = (<ul className= {classes['cart-items']}>

        {cartCtx.items.map(item => <CartItem key = {item.id} 
        name = {item.name} price = {item.price} amount = {item.amount} 
        onAdd ={cartItemAddHandler.bind(null,item)} onRemove ={cartItemRemoveHandler.bind(null,item.id)} />)}</ul>);


    return (
    <Modal onClose ={props.onClose}>

        {isLoading && <p>Submitting Meals Data</p>}
        {error && <p>{error}</p>}
        {isUserDataAdded && <p>Data Submitted Successfully</p>}
        {!isUserDataAdded && !error && !isLoading && <React.Fragment>
        <div>
        {cartItems}
        </div>
        <div className={classes.total}>
            <span>Total Amount</span>
            <span>{totalAmount}</span>
        </div>

        {isCheckout && <Checkout onClose={props.onClose} onSubmit={submitOrderHandler}/>}
            
        {!isCheckout && <div className={classes.actions}>
            <button className= {classes['button--alt']} onClick={props.onClose} >Close</button>
            {hasItems && <button className={classes.button} onClick={orderHandler}>Order</button>}

        </div>}
        </React.Fragment>}
        
    </Modal>);
}

export default Cart;